import { useState, useRef, useEffect } from 'react';
import { Bot, Loader2 } from 'lucide-react';
import { ChatMessage } from './ChatMessage';
import { ChatInput } from './ChatInput';
import { Button } from '@/components/ui/button';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import { ScrollArea } from '@/components/ui/scroll-area';
import { useToast } from '@/hooks/use-toast';

const EXAMPLE_QUESTIONS = [
  "What's the weather like today?",
  "Tell me a fun fact about space",
  "How can I improve my productivity?",
];

export default function ChatInterface() {
  const [messages, setMessages] = useState<Array<{
    text: string;
    sender: 'user' | 'bot';
    timestamp: string;
  }>>([]);
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const scrollAreaRef = useRef<HTMLDivElement>(null);
  const { toast } = useToast();

  const isExtension = Boolean(window.chrome && chrome.runtime && chrome.runtime.id);
  console.log('[ChatInterface] Running in extension mode:', isExtension);

  useEffect(() => {
    if (scrollAreaRef.current) {
      scrollAreaRef.current.scrollTo({
        top: scrollAreaRef.current.scrollHeight,
        behavior: 'smooth',
      });
    }
  }, [messages]);

  const handleSubmit = async () => {
    if (!inputValue.trim() || isLoading) return;
    console.log('[ChatInterface] Submitting message:', inputValue);

    const userMessage = {
      text: inputValue,
      sender: 'user' as const,
      timestamp: new Date().toLocaleTimeString(),
    };

    setMessages((prev) => [...prev, userMessage]);
    setInputValue('');
    setIsLoading(true);

    try {
      let response;
      const payload = {
        message: inputValue,
      };

      if (isExtension) {
        console.log('[ChatInterface] Sending through extension with payload:', payload);
        
        response = await new Promise((resolve, reject) => {
          const timeoutId = setTimeout(() => {
            reject(new Error('Extension request timed out after 30 seconds'));
          }, 30000);

          chrome.runtime.sendMessage(
            {
              type: 'API_REQUEST',
              endpoint: '/chat',
              data: payload
            },
            (result) => {
              clearTimeout(timeoutId);
              console.log('[ChatInterface] Extension response received:', result);
              
              if (chrome.runtime.lastError) {
                console.error('[ChatInterface] Extension error:', chrome.runtime.lastError);
                reject(new Error(chrome.runtime.lastError.message));
              } else if (result && result.error) {
                console.error('[ChatInterface] API error:', result.error);
                reject(new Error(result.error));
              } else if (!result || !result.response) {
                console.error('[ChatInterface] Invalid response format:', result);
                reject(new Error('Invalid response format from server'));
              } else {
                resolve(result);
              }
            }
          );
        });
      } else {
        console.log('[ChatInterface] Sending through localhost with payload:', payload);
        const fetchResponse = await fetch('/chat', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(payload),
        });

        if (!fetchResponse.ok) {
          throw new Error(`HTTP error! status: ${fetchResponse.status}`);
        }
        response = await fetchResponse.json();
        console.log('[ChatInterface] Localhost response:', response);
      }

      console.log('[ChatInterface] Processing response:', response);

      const botMessage = {
        text: response.response,
        sender: 'bot' as const,
        timestamp: new Date().toLocaleTimeString(),
      };

      setMessages((prev) => [...prev, botMessage]);
    } catch (error) {
      console.error('[ChatInterface] Error:', error);
      toast({
        title: 'Error',
        description: error instanceof Error ? error.message : 'Failed to get response. Please try again.',
        variant: 'destructive',
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleExampleClick = (question: string) => {
    setInputValue(question);
  };

  return (
    <Card className="w-full max-w-3xl mx-auto h-[600px] flex flex-col">
      <CardHeader className="border-b">
        <div className="flex items-center gap-2">
          <div className="p-2 rounded-md bg-primary/10">
            <Bot size={22} />
          </div>
          <div>
            <CardTitle>AI Assistant</CardTitle>
            <CardDescription>Powered by Groq</CardDescription>
          </div>
        </div>
      </CardHeader>
      <CardContent className="flex-1 flex flex-col p-0">
        <ScrollArea
          ref={scrollAreaRef}
          className="flex-1 p-4 space-y-4"
        >
          {messages.length === 0 ? (
            <div className="h-full flex flex-col items-center justify-center text-center p-8 space-y-4">
              <h3 className="font-semibold text-xl">Welcome! 👋</h3>
              <p className="text-muted-foreground">
                Try asking me anything or start with one of these examples:
              </p>
              <div className="flex flex-wrap gap-2 justify-center">
                {EXAMPLE_QUESTIONS.map((question) => (
                  <Button
                    key={question}
                    variant="outline"
                    onClick={() => handleExampleClick(question)}
                  >
                    {question}
                  </Button>
                ))}
              </div>
            </div>
          ) : (
            messages.map((message, index) => (
              <ChatMessage key={index} message={message} />
            ))
          )}
          {isLoading && (
            <div className="flex items-center gap-2 text-muted-foreground animate-pulse">
              <Loader2 size={18} className="animate-spin" />
              <span>AI is thinking...</span>
            </div>
          )}
        </ScrollArea>
        <ChatInput
          value={inputValue}
          onChange={setInputValue}
          onSubmit={handleSubmit}
          isLoading={isLoading}
        />
      </CardContent>
    </Card>
  );
}